/* eslint-disable import/no-unresolved */

import Plugin from 'src/script/plugin-system/plugin.class';
import './payone-payment.credit-card.scss';

export default class PayonePaymentCreditCard extends Plugin {
    static options = {
        supportedCardtypes: [
            '#',
            'V',
            'A',
            'M',
            'D',
            'J',
            'O',
            'U',
            'P'
        ],
    };

    init() {
        this.iframe = null;
        this.orderFormDisabled = false;

        this.createScript(() => {
            let config = this.getClientConfig([]);
            let request = [];

            this.iframe = new window.Payone.ClientApi.HostedIFrames(config, request);

            document
                .getElementById('confirmOrderForm')
                .addEventListener("submit", this.handleOrderSubmit);
        });
    }

    getSelectStyle() {
        const styles = [
            "width: 100%;",
            "height: calc(1.5em + 1.45rem);",
            "padding: .5625rem;",
            "color: #8798a9;",
            "vertical-align: middle;",
            "line-height: 1.5;",
            "font-weight: 500;",
            "background-color: #fff;",
            "border: .0625rem solid #d1d9e0;",
            "border-radius: 3px",
        ];

        return styles.concat();
    }

    getFieldStyle() {
        const styles = [
            "width: 100%;",
            "height: 100%;",
            "padding: .5625rem;",
            "color: #8798a9;",
            "vertical-align: middle;",
            "line-height: 1.5;",
            "font-weight: 500;",
            "background-color: #fff;",
            "border: .0625rem solid #d1d9e0;",
            "border-radius: .1875rem;",
        ];

        return styles.concat();
    }

    getClientConfig(language) {
        return {
            fields: {
                cardpan: {
                    selector: 'cardpan',
                    type: 'text',
                    style: this.getFieldStyle(),
                },
                cardcvc2: {
                    selector: 'cardcvc2',
                    type: 'password',
                    size: '4',
                    maxlength: '4',
                    style: this.getFieldStyle(),
                },
                cardexpiremonth: {
                    selector: 'cardexpiremonth',
                    type: 'select',
                    size: '2',
                    maxlength: '2',
                    style: this.getSelectStyle(),
                },
                cardexpireyear: {
                    selector: 'cardexpireyear',
                    type: 'select',
                    style: this.getSelectStyle(),
                }
            },

            language: language,

            defaultStyle: {
                iframe: {
                    height: '100%',
                    width: '100%'
                }
            },

            autoCardtypeDetection: {
                supportedCardtypes: PayonePaymentCreditCard.options.supportedCardtypes,
                callback: this.cardDetectionCallback
            },
        };
    }

    cardDetectionCallback(detectedCardtype) {
        if (detectedCardtype === "-" || detectedCardtype === "?") {
            return;
        }

        let src = 'https://cdn.pay1.de/cc/' + detectedCardtype.toLowerCase() + '/xl/default.png';

        let errorOutput = document.getElementById('errorOutput');
        let logo = document.getElementById('card-logo');

        logo.setAttribute("src", src);

        errorOutput.style.display = 'none';
        logo.style.display = 'block';
    }

    createScript(callback) {
        let url = 'https://secure.pay1.de/client-api/js/v1/payone_hosted_min.js';

        let script = document.createElement('script');
        script.type = 'text/javascript';
        script.src = url;

        script.addEventListener('load', callback.bind(this), false);

        document.head.appendChild(script);
    }

    handleOrderSubmit(event) {
        let errorOutput = document.getElementById('errorOutput');

        errorOutput.style.display = 'none';

        let savedCardPan = document.getElementById('savedpseudocardpan');

        if (savedCardPan && savedCardPan.value.length > 0) {
            return true;
        }

        if (!this.iframe.isComplete() || this.orderFormDisabled) {
            this.iframe.creditCardCheck('payoneCheckCallback');

            event.preventDefault();

            return false;
        }
    }

     payoneCheckCallback(response) {
        if (response.status === 'VALID') {
            document.getElementById('pseudocardpan').value = response.pseudocardpan;
            document.getElementById('truncatedcardpan').value = response.truncatedcardpan;

            this.orderFormDisabled = false;

            document.getElementById('confirmOrderForm').submit()
        } else {
            let button = document.getElementById('confirmFormSubmit');
            let errorOutput = document.getElementById('errorOutput');

            button.removeAttribute('disabled');

            // TODO: Find alternative for this line
            // button.find(".spinner-border").remove();

            errorOutput.innerHTML = response.errormessage;
            errorOutput.style.display = 'block';
        }
    }
}
